


































.no-network {
    height: 100vh;
    font-size: 0.373333rem;
    color: #999;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f2f7fa;

    img {
        height: 4.373333rem;
        width: 3.333333rem;
        margin-bottom: 0.533333rem;
    }
    .reload {
        height: 1.066667rem;
        border-radius: 0.533333rem;
        background-color: #2a8cff;
        font-size: 0.373333rem;
        color: #fff;
        width: 4.266667rem;
        margin-top: 0.533333rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
